<template>
  <nut-divider content-position="left">胜平负</nut-divider>
  <nut-row>
    <nut-col :span="9">
      <div class="left fontCoarse">时间</div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">胜</span><span class="u-img"></span>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">平</span><span class="u-img"></span>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content fontCoarse"><span class="u-batNum">负</span><span class="u-img"></span>
      </div>
    </nut-col>
  </nut-row>
  <nut-row v-for="(change, index) in hadList"
           :key="index">
    <nut-col :span="9">
      <div class="left fontCoarse">{{change.date}} {{change.updateTime}}</div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.h}}</span>
        <template v-if="change.hf == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.hf == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.d}}</span>
        <template v-if="change.df == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.df == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
    <nut-col :span="5">
      <div class="content"><span class="u-batNum">{{change.a}}</span>
        <template v-if="change.af == 1">
          <span class="u-img"><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_sjt.png"><!--v-if--></span>
        </template>
        <template v-else-if="change.af == -1">
          <span class="u-img"><!--v-if--><img alt=""
                 src="https://static.sporttery.cn/res_1_0/jcw/images/fotBasbal_match/icon_xjt.png"></span>
        </template>
        <template v-else>
          <span class="u-img"></span>
        </template>
      </div>
    </nut-col>
  </nut-row>
</template>

<script setup>
import { ref } from 'vue';
const hadList = ref([]);
const pushHadList = (list) => {
  if (list.length > 0) {
    for (let i = 0; i < list.length; i++) {
      let s = list[i].updateDate.split('-');
      list[i].date = s[1] + '-' + s[2];
    }
  }
  hadList.value = list;
}
// 暴露给外部使用的函数
defineExpose({
  pushHadList
});
</script>

<style scoped>
.u-img {
  width: 11px;
  display: inline-block;
}
.u-batNum {
  padding: 0 2px;
}
</style>